import React, {useState} from "react";
import {art} from "../interfaces";

interface props {
    handleAddArt: (art: art) => void
}

const AddArt = ({handleAddArt}: props) => {
    const [file, setFile] = useState("");
    const [name, setName] = useState("");
    const [hebName, setHebName] = useState("");
    const [size, setSize] = useState("");

    return <div className="addArt">
        <input type="file" accept="image/png,image/jpeg" onChange={(event) => setFile((event.target.value))}/>
        <br/>
        <input
            id="name"
            type="text"
            value={name}
            onChange={(event) =>
                setName(event.target.value)}
        />
        <br/>
        <input
            id="heeName"
            type="text"
            value={hebName}
            onChange={(event) =>
                setHebName(event.target.value)}
        />
        <br/>
        <input
            id="size"
            type="text"
            value={size}
            onChange={(event) =>
                setSize(event.target.value)}
        />
        <br/>
        <button onClick={() => handleAddArt({file, name, hebName, size})}>Add Art</button>
    </div>
};

export default AddArt;