import axios from "axios";
import {art} from "../interfaces";
import {Octokit} from "octokit";
import {encode} from "js-base64"

const editJson = async (newJson: art[]) => {
    try {

        const octokit = new Octokit({
            auth: "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"
        })

        const getSha = await octokit.request("GET /repos/iAmIdc/Gallary/contents/index.json", {
                owner: 'iAmIdc',
                repo: 'Gallary',
                path: 'index.json'
            }
        )

        await octokit.request('PUT /repos/iAmIdc/Gallary/contents/index.json', {
            owner: 'iAmIdc',
            repo: 'Gallary',
            path: 'index.json',
            message: 'eddited josn',
            committer: {
                name: 'app',
                email: "test@test.com"
            },
            content: encode((JSON.stringify(newJson))),
            sha: getSha.data.sha,
            headers: {
                'X-GitHub-Api-Version': '2022-11-28'
            }
        })
    } catch (error) {
        console.log(error)
    }
}

const getJson = async () => {
    try {
        const data = await axios.get(
            "https://raw.githubusercontent.com/iAmIdc/Gallary/main/index.json",
            {});
        return data.data;
    } catch {
        return ([]);
    }
}

export {getJson, editJson}