import './App.css';
import {ReactNode, useEffect, useState} from "react";
import {editJson, getJson} from "./utils";
import Art from "./art/Art";
import {art, editedArt} from "./interfaces";
import AddArt from "./AddArt/AddArt";
import EditedArt from "./EditedArt/EditedArt";
import Modal from "./Modal/Modal";

const App = () => {
    const [galleryInfo, setGalleryInfo] = useState([] as art[]);
    const [page, setPage] = useState(0);
    const [isEditMode, setIsEditMode] = useState(false);
    const [popUp, setPopUp] = useState(false);

    const handleCorrectPassword = () => {
        setPopUp(false);
        setIsEditMode(true);
    }
    const handleChange = async () => {
        setIsEditMode(false);
        await editJson(galleryInfo);
    };

    const handleCancel = () => {
        getJson().then((data) => setGalleryInfo(data));
        setIsEditMode(false);
    }

    const handleAddArt = (art: art) => {
        console.log(art);
    };

    useEffect(() => {
        getJson().then((data) => setGalleryInfo(data));
    }, []);

    const handleGalleryChange = (editedArt: editedArt, file: string) => {
        setGalleryInfo(prev => prev.map(art =>
            art.file === file ?
                ({...editedArt, file}) :
                art
        ))
    }

    return (
        <>
            <h1>
                <span onClick={() => setPopUp(true)}>A</span>lon Slakman Gallery
            </h1>
            <h2>
                i dont got hebrew on this compopar :(
            </h2>
            <div className="paper">
                {isEditMode ? <AddArt handleAddArt={handleAddArt}/> : ""}
                {galleryInfo.filter((_, index) => Math.floor(index / 9) === page)
                    .map(art => !isEditMode ? <Art
                        key={art.file}
                        art={art}
                    /> : <EditedArt
                        art={art}
                        key={art.file}
                        setGalleryInfo={(editedArt) => handleGalleryChange(editedArt, art.file)}
                    />)}
                <div>
                    {galleryInfo.reduce((steps: ReactNode[], _, index) =>
                            !(index % 9)
                                ? [...steps,
                                    <button
                                        onClick={() => setPage(index / 9)}
                                        style={{
                                            color: index / 9 === page ? "hsl(231deg 100% 41%)" : "hsl(120deg 50% 20%)",
                                            fontWeight: index / 9 === page ? "bold" : "normal",
                                        }}
                                        key={index}>
                                        {(index / 9) + 1}
                                    </button>
                                ]
                                : steps,
                        []
                    )}
                </div>
            </div>
            {isEditMode ? <>
                    <button onClick={handleChange}>save</button>
                    <button onClick={handleCancel}>cancel</button>
                </>
                : ""}
            {popUp ?
                <Modal
                    handleCorrectPassword={handleCorrectPassword}
                    close={() => setPopUp(false)}/> : ""}
        </>
    );
}

export default App;
