import {art, editedArt} from "../interfaces";
import React, {SetStateAction, useState} from "react";

interface props {
    art: art,
    setGalleryInfo: (editedArt: editedArt) => void;
}

const EditedArt = ({art, setGalleryInfo}: props) => {
    const [name, setName] = useState(art.name ? art.name : "untitled");
    const [hebName, setHebName] = useState(art.hebName ? art.hebName : "");
    const [size, setSize] = useState(art.size ? art.size : "");

    const handleChange = (setState: React.Dispatch<SetStateAction<string>>, value: string, type: string) => {
        setState(value);
        setGalleryInfo(({name, hebName, size, [type]: value}))
    }

    return <div className="imageContainer">
        <img
            src={
                `https://raw.githubusercontent.com/iAmIdc/Gallary/main/images/${art.file}`
            }
            alt={name}
            className="image"/>
        <div>
            <input
                id={"name"+art.file}
                type="text"
                value={name}
                onChange={(event) =>
                    handleChange(setName, event.target.value, "name")}
            />
            <br/>
            <input
                id={"hebName"+art.file}
                type="text"
                value={hebName}
                onChange={(event) =>
                    handleChange(setHebName, event.target.value, "hebName")}
            />
            <br/>
            <input
                id={"size"+art.file}
                type="text"
                value={size}
                onChange={(event) =>
                    handleChange(setSize, event.target.value, "size")}
            />
        </div>
    </div>
}

export default EditedArt;