import {art} from "../interfaces";

interface props {
    art: art,
}

const Art = ({art}: props) => {
    const {name = "untitled", hebName, size} = art;

    return <div className="imageContainer">
        <img
            src={
                `https://raw.githubusercontent.com/iAmIdc/Gallary/main/images/${art.file}`
            }
            alt={name}
            className="image"/>
        <div>
            {name}
            <br/>
            {hebName}
            <br/>
            {size}
        </div>
    </div>
}

export default Art;